
import XButton from 'vux/src/components/x-button/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'


export default {
name: "",
props: ["dataList"],
data() {
return {
backgroundImgSty: {
width: "100%",
height: "400px"
}
};
},

components: {
XButton,
InlineLoading
},

computed: {},

beforeMount() {},

mounted() {},

methods: {
getDetailsContent(id) {
let type = this.$route.query.type;
let branch = this.$route.query.branch;
this.$router.push(
"/scencestorydetail?id=" + id + "&type=" + type + "&branch=" + branch
);
}
},

watch: {}
};



